import uniLoadMore from '../uni-load-more/uni-load-more.vue';
	export default {
		data() {
			return {
				waitForAsync:true,
				rpx:1,
				bodyHeight:0,
				topHeight:0,
				searchTab:[{title:"待内推"},{title:"已内推"}],
				activeTab:0,
				
				listArray1:[],
				pageNum1:0,
				loadingType1: 0,
				contentText1: {
					contentdown: '上拉显示更多',
					contentrefresh: '正在加载...',
					contentnomore: '没有更多数据了'
				},
				
				listArray2:[],
				pageNum2:0,
				loadingType2: 0,
				contentText2: {
					contentdown: '上拉显示更多',
					contentrefresh: '正在加载...',
					contentnomore: '没有更多数据了'
				},
			}
		},
		components: {
			uniLoadMore
		},
		onLoad() {
			uni.getSystemInfo({
				success: res => {
					this.rpx = res.windowWidth / 750;
					this.topHeight = 102 * this.rpx;
					this.bodyHeight = res.windowHeight - this.topHeight;
				}
			});
		},
		onShow() {
			let initPage = uni.getStorageSync("receivedResume");
			if(initPage == "2"){//通过点击查看简历回退回来的 不做处理
				
			}else{
				this.activeTab = 0;
				this.waitForAsync = true;
				this.listArray1 = [];
				this.pageNum1 = 0;
				this.loadingType1 = 0;
				this.getDate1();
			}
		},
		methods: {
			getTime(time){
				if(time){
					return time.split(" ")[0];
				}else{
					return "";
				}
			},
			toFastSearch(e){//快捷搜索点击
				var index = e.currentTarget.dataset.index;
				this.activeTab = index;
				if(index == 0){
					uni.setStorageSync("receivedResume",1);
					this.waitForAsync = true;
					this.listArray1 = [];
					this.pageNum1 = 0;
					this.loadingType1 = 0;
					this.getDate1();
					
				}else{
					this.waitForAsync = false;
					this.listArray2 = [];
					this.pageNum2 = 0;
					this.loadingType2 = 0;
					this.getDate2();
				}
			},
			getDate1(){
				let _this = this;
				_this.pageNum1++;
				_this.unirequest({
					url: '/position/pendingPaging',
					data: {
						pageNum: _this.pageNum1,
						pageSize: 10,
					},
					success: res => {
						if(res){
							console.log(res,"resss")
							_this.listArray1 = _this.listArray1.concat(res);
							if(res.length<10){
								_this.loadingType1 = 2;
							}else{
								_this.loadingType1 = 0;
							}
						}else{
							var loginStatu = this.getLoginStatus();
							if(!res && !loginStatu){
								console.log("请求中未登录")
								uni.navigateTo({url: '../../pages/login'});
							}
						}
					},
					fail:e=>{
						console.log(e);
					}
				});
			},
			getDate2(){
				let _this = this;
				_this.pageNum2++;
				_this.unirequest({
					url: '/position/pushedPaging',
					data: {
						pageNum: _this.pageNum2,
						pageSize: 10,
					},
					success: res => {
						if(res){
							console.log(res,"resss")
							_this.listArray2 = _this.listArray2.concat(res);
							if(res.length<10){
								_this.loadingType2 = 2;
							}else{
								_this.loadingType2 = 0;
							}
						}else{
							var loginStatu = this.getLoginStatus();
							if(!res && !loginStatu){
								console.log("请求中未登录")
								uni.navigateTo({url: '../../pages/login'});
							}
						}
					},
					fail:e=>{
						console.log(e);
					}
				});
			},
			scrollToLower1(){
				if(this.loadingType1 == 2)return;
				this.loadingType1 = 1;
				this.getDate1();
			},
			scrollToLower2(){
				if(this.loadingType2 == 2)return;
				this.loadingType2 = 1;
				this.getDate2();
			},
			checkResume(e){//查看简历			
				// let id = e.currentTarget.dataset.id;
				let id = e.currentTarget.dataset.id;
				let email = e.currentTarget.dataset.email;
				let name = e.currentTarget.dataset.name;
				let mobile = e.currentTarget.dataset.mobile;
				let cvFileName = e.currentTarget.dataset.cvfilename;
				let cvUrl = e.currentTarget.dataset.cvurl;
				uni.setStorageSync("receivedResume",2);
				uni.navigateTo({url: '../resumeDetail/resumeDetail?id='+id+'&email='+email+'&name='+name
				+'&mobile='+mobile+'&cvFileName='+cvFileName+'&cvUrl='+cvUrl});
			}
			
		}
	}
